.btn.btn-primary {
  width: 100%;
  padding: 10px 30px 8px 30px;
  margin: 0;
  min-height: 46px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.8s;
  line-height: 24px;
  background-color: #62C2CE;
  border: 1px solid rgba(0, 36, 17, 0.1);
  border-radius: 8px;
  font-family: "Avenir LT Std 65";
  font-size: 14px;
  color: #FFFFFF;
  text-align: center;
  text-transform: uppercase;
  transition: all 0.8s !important;
}

.btn.btn-primary:hover, .btn.btn-primary:focus, .btn.btn-primary:active {
  background-color: #62C2CE;
  border: 1px solid rgba(0, 36, 17, 0.1);
  outline: none;
  box-shadow: 0 3px 10px 0 rgba(12, 64, 107, 0.3);
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

.btn.btn-primary.disabled {
  background: rgba(192, 192, 192, 0.5);
  border: 1px solid rgba(0, 36, 17, 0.1);
}

.btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active, .show > .btn-primary.dropdown-toggle {
  background-color: #62C2CE;
  border: 1px solid rgba(0, 36, 17, 0.1) !important;
  outline: none;
  box-shadow: 0 3px 10px 0 rgba(12, 64, 107, 0.3);
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

.btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus, .show > .btn-primary.dropdown-toggle:focus {
  box-shadow: 0 3px 10px 0 rgba(12, 64, 107, 0.3);
}

.btn.btn-secondary {
  width: 100%;
  padding: 10px 30px 8px 30px;
  margin: 0;
  min-height: 46px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.8s;
  line-height: 24px;
  background-color: #FFFFFF;
  border: 1px solid rgba(192, 192, 192, 0.6);
  border-radius: 8px;
  font-family: "Avenir LT Std 65";
  font-size: 14px;
  color: #62C2CE;
  text-align: center;
  text-transform: uppercase;
  transition: all 0.8s !important;
}

.btn.btn-secondary:hover, .btn.btn-secondary:focus, .btn.btn-secondary:active {
  background-color: #FFFFFF;
  border: 1px solid rgba(192, 192, 192, 0.6);
  color: #62C2CE;
  border: 1px solid rgba(0, 36, 17, 0.1);
  outline: none;
  box-shadow: 0 3px 10px 0 rgba(12, 64, 107, 0.1);
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

.btn-secondary:not(:disabled):not(.disabled).active:focus, .btn-secondary:not(:disabled):not(.disabled):active:focus, .show > .btn-secondary.dropdown-toggle:focus {
