.MuiTableCell-root {
  border: 1px solid #eee;
  padding: 8px !important;
  max-width: 100px;
}

.MuiTableCell-head {
  font-weight: 600 !important;
}

::-webkit-scrollbar {
  width: 4px;
   background-color: rgb(252, 252, 252);
}

::-webkit-scrollbar-thumb {
  background-color: #9b9b9b;
  border-radius: 20px;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #F5F5F5;
}



  .root {
    width: 100%;
      height: 600px;
      overflow: auto
  }

  .paper {
    width: 100%;
      margin-bottom: 16;
  }

  .table {
  }

  .visuallyHidden {
    border: 0;
      clip: rect(0 0 0 0);
      height: 1;
      margin: -1;
      overflow: hidden;
      padding: 0;
      position: absolute;
      top: 20;
      width: 1;
  }

  .icons {
    padding: 0
  }

  .floatRight {
    float: right;
      margin-right: 8px
  }


  .disable {
    pointer-events: none
  }


  .fixedBox {
    min-height: 90px;
      max-height: 90px;
      overflow: auto;
      display: flex;
      align-items: center
  }

  .fixedBox span{
        min-height: 90px;
        max-height: 90px;
  }

  .error {
    color: red;
      display: flex;
      align-items: center
  }